body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contain{
    max-width: 900px;
    margin-top: 20px;
}

.m-left{
    margin-left: 20px;
}
.flow{
    font-family: Haettenschweiler;
    color: white;
    font-size: 50px;
    text-decoration: none;
    margin: 0;
    position: absolute;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.flow:hover{
    color: orange;

    text-decoration: none;
}
@media screen and (max-width: 991px){
    .flow{
        display: none;
    }
}
.header button{
    height: 2.2rem;

}

.h2{
    color:#B17828;
    font-family: "Agency FB";
}
.p{
    color: #B17828;
    font-family: "Agency FB";
    font-size: large;
}

.err{
    display: block;
    width: 400px;
    height: 400px;
    margin: 125px 0 0 425px;
}

li{
    list-style: none;
}
hr{
    background-color: gray;

}

.info{
    float: right;
}
.img{

}
.random-planet{
    border-radius: 5px;
    background-color: #2C2C2C;
    padding: 10px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.m-20{
    margin-bottom: 10px;
}

@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
.lds-double-ring {
    position: relative;
}
.lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #1d0e0b transparent #1d0e0b transparent;
    -webkit-animation: lds-double-ring 2.4s linear infinite;
    animation: lds-double-ring 2.4s linear infinite;
}
.lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent #FFA500 transparent #FFA500;
    -webkit-animation: lds-double-ring_reverse 2.4s linear infinite;
    animation: lds-double-ring_reverse 2.4s linear infinite;
}
.lds-double-ring {
    width: 97px !important;
    height: 97px !important;
    -webkit-transform: translate(-48.5px, -48.5px) scale(0.485) translate(48.5px, 48.5px);
    transform: translate(-48.5px, -48.5px) scale(0.485) translate(48.5px, 48.5px);
}

.person-details{
    background-color: #2C2C2C;
    margin-top: 20px;
    padding: 5px;
}
img{
    border-radius: 5px;
}

.path{
    background-color: #2C2C2C;
    margin: 15px;
    margin-left: 0;
    padding: 20px;
    border-radius: 5px;
    width: 100%;
}
.list-group-item{
    -webkit-transition: 0.2s;
    transition: 0.2s;
}
.list-group-item:hover{
    background-color: white;
    color: black;

}

