@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
.lds-double-ring {
    position: relative;
}
.lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #1d0e0b transparent #1d0e0b transparent;
    -webkit-animation: lds-double-ring 2.4s linear infinite;
    animation: lds-double-ring 2.4s linear infinite;
}
.lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent #FFA500 transparent #FFA500;
    -webkit-animation: lds-double-ring_reverse 2.4s linear infinite;
    animation: lds-double-ring_reverse 2.4s linear infinite;
}
.lds-double-ring {
    width: 97px !important;
    height: 97px !important;
    -webkit-transform: translate(-48.5px, -48.5px) scale(0.485) translate(48.5px, 48.5px);
    transform: translate(-48.5px, -48.5px) scale(0.485) translate(48.5px, 48.5px);
}
